import { Component, HostListener } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'app';
  row: string = '';
  actionBtn: string = '';
  firstValue: number = 0;
  secondValue: number = 0;
  clicked: boolean = false;
  placeholder = '0';

  first;

  get clickedStyle() {
    return this.clicked ? 'gray' : '';
  }

  constructor() {

  }

  @HostListener('window:keydown', ['$event'])
  keyboardInput(event: any) {
    event.preventDefault();
    event.stopPropagation();
    
    if ( event.ctrlKey || event.altKey 
      || (47<event.keyCode && event.keyCode<58 && event.shiftKey==false) 
      || (95<event.keyCode && event.keyCode<106)
      || (event.keyCode==8) 
      || (event.keyCode==190)
      || (event.keyCode==110)
      || (event.keyCode === 107)
      || (event.keyCode === 111)
      || (event.keyCode === 106)
      || (event.keyCode === 109)
      || (event.keyCode === 13) ) {
        if (event.keyCode === 107) {
          this.action('sum');
          return;
        }

        if (event.keyCode === 111) {
          this.action('divide');
          return;
        }

        if (event.keyCode === 106) {
          this.action('multiply');
          return;
        }

        if (event.keyCode === 109) {
          this.action('deduct');
          return;
        }

        if (event.keyCode === 13) {
          this.equal();
          return;
        }

        if (event.keyCode==8) {
          if (this.actionBtn !== '') return;

          this.row = this.row.slice(0, -1);
          this.placeholder = '0';
          return;
        }

        this.addNumber(event.key);
    }
  }

  /** Действие */
  action(name) {
    this.actionBtn = this.actionBtn === name ? '' : name;
    this.firstValue = +this.row;
    this.placeholder = this.row;
    this.row = '';
  }

  /** Нажатие на цифру или запятую*/
  addNumber(number: number | string) {
    if (number === '.') {
      const index = this.row.indexOf('.');      
      if (index !== -1) return;
    }

    if (this.row === '0' && (number === 0 || number === '0')) return;
   
    this.row = this.row + number;
  }

  /** Равно */
  equal() {
    const second: number = +this.row;

    switch(this.actionBtn) {
      case 'sum'     : this.actionBtn = ''; return this.sum(second);;
      case 'multiply': this.actionBtn = ''; return this.multiply(second);
      case 'deduct'  : this.actionBtn = ''; return this.deduct(second);
      case 'divide'  : this.actionBtn = ''; return this.divide(second);
      default        : return;
    }
  }

  /** Процент */
  percent() {
    this.row = (+this.row / 100).toString(); 
  }

  addMinus() {
    this.row = '-' + this.row;
  }

  /** Прибавить */
  private sum(value: number) {
    this.row = (this.firstValue + value).toString();
  }
  /** Умножить */
  private multiply(value: number) {
    this.row = (this.firstValue * value).toString();
  }
  /** Минус */
  private deduct(value: number)  {
    this.row = (this.firstValue - value).toString();
  }

  /** Делить */
  private divide(value: number) {
    this.row = (this.firstValue / value).toString();
  }

  /** Очистить */
  clear(e) {
    this.row = '';
    this.actionBtn = '';
    this.firstValue = 0;
    this.placeholder = '0';
  }
}
